import React, { Component } from 'react';
import { Row, Col, Form, Input, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common/index';
import { getDormitorys, getDormitoryFloors } from 'services/dormitory';

@Form.create({ name: 'FormSearchDormitoryRoom' })
class FormSearchDormitoryRoom extends Component {
  state = {};

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.props.onSubmit(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const dormId = getFieldValue('dormId');
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="Tòa KTX">
              {getFieldDecorator('dormId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<SelectAjax service={getDormitorys} />)}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Tầng">
              {getFieldDecorator('floorId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  params={{ dormId }}
                  service={getDormitoryFloors}
                  allowGetData={!!dormId}
                />
              )}
            </Form.Item>
          </Col>

          <Col span={this.formItemLayout}>
            <Form.Item label=" ">
              <div className="btn-group">
                <BtnSearch htmlType="submit" />
                <BtnClear onClick={this.handleReset} />
              </div>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchDormitoryRoom;



